import React from 'react';
import { Drawer } from 'antd';
import classes from './DrawerLeft.module.scss';

// Components
import Search from '../Search/Search';
import DrawerLeftLinks from '../DrawerLeftLinks/DrawerLeftLinks';

function DrawerLeft({ visible, onClose }) {
	return (
		<Drawer
			placement="left"
			width={300}
			className={classes.drawerleft}
			closable={false}
			onClose={onClose}
			visible={visible}
		>
			<div className={classes.drawerLeft}>
				<Search />
				<DrawerLeftLinks />
			</div>
		</Drawer>
	);
}

export default DrawerLeft;
