import React, { useState } from 'react';
import classes from './NavMain.module.scss';
import { Link } from 'gatsby';
import Hamburger from 'hamburger-react';

import { connect } from 'react-redux';

function NavMain({ showDrawer, showRight, visible, itemsInCart }) {
	const [ isOpen, setOpen ] = useState(false);
	return (
		<div className={classes.cont}>
			<div role="button" style={{ cursor: 'pointer', zIndex: 9999 }} onClick={showDrawer}>
				<Hamburger size={20} rounded toggled={visible} toggle={setOpen} />
			</div>
			<div>
				<b className="m-0 p-0 ml-4">
					<Link className={classes.logolink} to="/">
						<b className={classes.logo}>KKW</b>
					</Link>
				</b>
			</div>
			<div role="button" style={{ cursor: 'pointer' }} onClick={showRight}>
				<p className={classes.basket}>
					<span style={{ fontSize: '.8em' }} className="d-inline ml-1">
						{`(${itemsInCart.toLocaleString('ar-EG')})`}
					</span>
					سبد من
				</p>
			</div>
		</div>
	);
}

const mapState = (state) => {
	return {
		itemsInCart: state.itemsInCart
	};
};
const mapDis = (dispatch) => {
	return {};
};

export default connect(mapState, mapDis)(NavMain);
