import React, { useState, createRef } from 'react';
import classes from './NewArrivals.module.scss';

function NewArrivals() {
	const emailRef = createRef();
	const [ value, setValue ] = useState('');
	const handleSubmit = () => {
		if (!value.length) {
			emailRef.current.focus();
		} else {
			// fetch
		}
	};
	return (
		<div className={classes.news}>
			<div className="container ">
				<p className={classes.text}>از دست نده</p>
				<p className={classes.text}>جهت اطلاع از محصولات جدید ایمیلت رو ثبت کن</p>
				<div style={{ background: 'none' }} dir="rtl" className="d-flex mt-3">
					<input
						type="text"
						className={classes.input}
						value={value}
						ref={emailRef}
						onChange={(e) => setValue(e.target.value)}
						placeholder="YOUR@EMAIL.COM"
					/>
					<button className={classes.signup} onClick={handleSubmit} type="submit">
						ثبت ایمیل
					</button>
				</div>
				<hr className="m-0 mt-1" style={{ background: 'black' }} />
				<p className={`${classes.text} mt-2`}>لطفا ایمیل معتبر وارد کنید</p>
			</div>
		</div>
	);
}

export default NewArrivals;
