import React from 'react';
import classes from './NavTop.module.scss';

function NavTop() {
	const vir = <b style={{ background: 'none' }}>COVID-19</b>;
	return (
		<div className={classes.topnav}>
			<p className={classes.txt}>
				ارسال رایگان تمامی محصولات برای خرید های بالای یک میلیون تومان بدلیل ویروس {vir}
			</p>
		</div>
	);
}

export default NavTop;
