import React, { Fragment, useState } from 'react';

// Components
import NavTop from '../NavTop/NavTop';
import NavMain from '../NavMain/NavMain';
import NavLinks from '../NavLinks/NavLinks';
import DrawerLeft from '../DrawerLeft/DrawerLeft';
import DrawerRight from '../DrawerRight/DrawerRight';

function Nav() {
	const [ visible, setVisible ] = useState(false);
	const [ visRight, setVisRight ] = useState(false);
	const showDrawer = () => {
		setVisible((prev) => !prev);
	};
	const onClose = () => {
		setVisible(false);
	};
	const onCloseRight = () => {
		setVisRight(false);
	};
	const showRight = () => {
		setVisRight((prev) => !prev);
	};
	return (
		<Fragment>
			<DrawerLeft visible={visible} onClose={onClose} />
			<DrawerRight visRight={visRight} onCloseRight={onCloseRight} />
			<NavTop />
			<NavMain visible={visible} showRight={showRight} showDrawer={showDrawer} setOpen={onClose} />
			<NavLinks />
		</Fragment>
	);
}

export default Nav;
