import React from 'react';
import { Drawer } from 'antd';
import { Link } from 'gatsby';

// Redux
import { connect } from 'react-redux';
import { increase, decrease, remove } from '../../redux/action';

// Components
import Items from '../Items/Items';
import Total from '../Total/Total';

function DrawerRight({ visRight, onCloseRight, itemsInCart, items, increase, decrease, remove, totalPrice }) {
	return (
		<Drawer
			key="right"
			placement="right"
			width="328px"
			maskClosable={true}
			closable={true}
			onClose={onCloseRight}
			visible={visRight}
			style={{ zIndex: 999999999999 }}
		>
			{items.length ? (
				<p dir="rtl">
					<span style={{ fontSize: '.8em' }} className="d-inline ml-1">
						{`(${itemsInCart.toLocaleString('ar-EG')})`}
					</span>
					سبد شما
				</p>
			) : null}
			<div style={{ paddingBottom: '5em' }}>
				{items.length ? (
					items.map((each) => {
						return (
							<Items
								img={each.img}
								title={each.title}
								price={each.price}
								inCart={each.inCart}
								addOnClick={() => increase(each.title)}
								removeOnClick={() => decrease(each.title)}
								deleteOnClick={() => remove(each.title)}
							/>
						);
					})
				) : (
					<div className="text-center mt-5">
						<h4>سبد شما خالیست</h4>
						<Link to="/shop">خرید</Link>
					</div>
				)}
				{items.length ? <Total total={totalPrice} /> : null}
			</div>
		</Drawer>
	);
}
const mapState = (state) => {
	return {
		itemsInCart: state.itemsInCart,
		items: state.items,
		totalPrice: state.totalPrice
	};
};
const mapDis = (dispatch) => {
	return {
		increase: (title) => dispatch(increase(title)),
		decrease: (title) => dispatch(decrease(title)),
		remove: (title) => dispatch(remove(title))
	};
};

export default connect(mapState, mapDis)(DrawerRight);
