import React from 'react';
import classes from './Total.module.scss';

function Total({ total }) {
	return (
		<div className={classes.back}>
			<div className="d-flex justify-content-between">
				<p className={classes.price}>{total.toLocaleString('ar-EG')} هزار تومان</p>
				<p className={classes.total}>جمع کل</p>
			</div>

			<div className="mr-3 mb-2 ml-3">
				<button className={classes.btn}>خرید</button>
			</div>
		</div>
	);
}

export default Total;
