import { CALL, ADD, INCREASE, DECREASE, REMOVE } from './type';

export const callAction = () => {
	return {
		type: CALL
	};
};

export const add = (item) => {
	return {
		type: ADD,
		payload: item
	};
};

export const increase = (title) => {
	return {
		type: INCREASE,
		payload: title
	};
};
export const decrease = (title) => {
	return {
		type: DECREASE,
		payload: title
	};
};

export const remove = (title) => {
	return {
		type: REMOVE,
		payload: title
	};
};
