import React, { Fragment } from 'react';
import { Link } from 'gatsby';
import classes from './DrawerLeftLinks.module.scss';

function DrawerLeftLinks() {
	return (
		<Fragment>
			<p className={classes.new}>جدید</p>
			<Link to="/shop" className={classes.link}>
				لوازم استاک
			</Link>
			<Link to="/shop" className={classes.link}>
				محصولات روزمره
			</Link>
			<Link to="/shop" className={classes.link}>
				پرفروش ترین
			</Link>
			<Link to="/shop" className={classes.link}>
				فروشگاه
			</Link>
			<div className="mr-4">
				<Link to="/shop" className={classes.linklight}>
					کلکسیون
				</Link>
				<Link to="/lips" className={classes.linklight}>
					رژ لب
				</Link>
				<Link to="/body" className={classes.linklight}>
					صورت و بدن
				</Link>
			</div>
			<Link to="/kit" className={classes.link}>
				باندل ها
			</Link>
			<Link to="/" className={classes.link}>
				درباره ما
			</Link>
		</Fragment>
	);
}

export default DrawerLeftLinks;
