import React from 'react';
import classes from './Items.module.scss';

function Items({ addOnClick, removeOnClick, deleteOnClick, img, title, inCart, price }) {
	const handleDisable = inCart > 1 ? false : true;
	return (
		<div className={classes.cont}>
			<div className={classes.contimg}>
				<img alt="product" src={img} className="img-fluid" />
			</div>
			<div className={classes.conttext}>
				<p className={classes.title}>{title}</p>
				<p className={classes.price}>{price.toLocaleString('ar-EG')} هزارتومان</p>
				<div className={classes.btns}>
					<button onClick={() => addOnClick()} className={classes.btn}>
						+
					</button>
					<p className="m-0">{inCart.toLocaleString('ar-EG')}</p>
					<button disabled={handleDisable} onClick={() => removeOnClick()} className={classes.btn}>
						-
					</button>
				</div>

				<div className="d-flex justify-content-end mt-3">
					<button onClick={() => deleteOnClick()} className={classes.btnremove}>
						حذف
					</button>
				</div>
			</div>
		</div>
	);
}

export default Items;
