import React from 'react';
import { Link } from 'gatsby';
import classes from './NavLinks.module.scss';

function NavLinks() {
	return (
		<div className={classes.whole}>
			<ul className="d-flex justify-content-center m-0 p-0" style={{ display: 'flex' }}>
				<li className={classes.hover}>
					<Link to="/" className={classes.navlinks}>
						گیفت کارت
					</Link>
				</li>
				<li className={classes.hover}>
					<Link to="/" className={classes.navlinks}>
						درباره ما
					</Link>
				</li>
				<li className={classes.hover}>
					<Link to="/shop" className={classes.navlinks}>
						محصولات روزمره
					</Link>
				</li>
				<li className={classes.hover}>
					<Link to="/kit" className={classes.navlinks}>
						باندل
					</Link>
				</li>
				<li className={classes.hover}>
					<a className={classes.navlinks}>کیت</a>
					<div className={classes.hovered}>
						<div className={classes.content}>
							<div dir="rtl" className="row">
								<div className="col-6">
									<Link to="/" className={classes.contentlink}>
										جدید ترین
									</Link>
									<Link to="/" className={classes.contentlink}>
										محصولات استاک
									</Link>
									<Link to="/" className={classes.contentlink}>
										بزودی
									</Link>
								</div>
								<div className="col-6">
									<Link to="/" className={classes.contentlink}>
										جدید ترین
									</Link>
									<Link to="/" className={classes.contentlink}>
										محصولات استاک
									</Link>
									<Link to="/" className={classes.contentlink}>
										بزودی
									</Link>
								</div>
							</div>
						</div>
					</div>
				</li>
				<li className={classes.hover}>
					<Link to="/lips" className={classes.navlinks}>
						رژ لب
					</Link>
				</li>
				<li className={classes.hover}>
					<Link to="/body" className={classes.navlinks}>
						صورت و بدن
					</Link>
				</li>
				<li className={classes.hover}>
					<a className={classes.navlinks}>چشم</a>
					<div className={classes.hovered}>
						<div className={classes.content}>
							<div dir="rtl" className="row">
								<div className="col-6">
									<Link to="/shop" className={classes.contentlink}>
										جدید ترین
									</Link>
									<Link to="/shop" className={classes.contentlink}>
										محصولات استاک
									</Link>
									<Link to="/shop" className={classes.contentlink}>
										بزودی
									</Link>
								</div>
							</div>
						</div>
					</div>
				</li>
				<li className={classes.hover}>
					<a className={classes.navlinks}>جدید</a>
					<div className={classes.hovered}>
						<div className={classes.content}>
							<div dir="rtl" className="row">
								<div className="col-6">
									<Link to="/kit" className={classes.contentlink}>
										کیت و باندل
									</Link>
									<Link to="/shop" className={classes.contentlink}>
										محصولات استاک
									</Link>
									<Link to="/lips" className={classes.contentlink}>
										محصولات رژ لب
									</Link>
								</div>
								<div className="col-6">
									<Link to="/body" className={classes.contentlink}>
										محصولات صورت و بدن
									</Link>
								</div>
							</div>
						</div>
					</div>
				</li>
			</ul>
		</div>
	);
}

export default NavLinks;
