import React, { Fragment } from 'react';
import classes from './FooterBottom.module.scss';
import badge from '../../img/badge.png';

function FooterBottom() {
	return (
		<Fragment>
			<div>
				<p className={classes.text}>&copy; 2020 MY BEAUTY. ALL RIGHTS RESERVED.</p>
				<div>
					<a href="" className={classes.link}>
						قوانین سایت
					</a>
					<a href="" className={`ml-3 ${classes.link}`}>
						موارد استفاده
					</a>
				</div>
			</div>
			<div>
				<img className="d-block" style={{ width: '60px' }} src={badge} />
			</div>
		</Fragment>
	);
}

export default FooterBottom;
